<template>
  <main class="mt-5">
    <loading :active.sync="isLoading"></loading>
    <Jumbotron/>
    <div class="container mt-3">
      <p class="text-center bg-warning py-3">2019/5/15 前，使用優惠碼「welcome」，購物可享 9 折優惠。</p>
    </div>
    <div class="container my-5">
      <div class="row">
        <div class="col-md-3">
          <!-- 左側選單 (List group) -->
          <Sidebar @emitCategory="switchCategory"></Sidebar>
        </div>
        <div class="col-md-9">
          <!-- <AllItem></AllItem> -->
          <div class="tab-content">
            <div class="tab-pane active">
              <div class="row">
                <div class="col-md-6 mb-4" v-for="item in filterData" :key="item.id">
                  <div class="card box-shadow border-0 h-100">
                    <div style="height: 200px; background-size: cover; background-position: 50% 30%"
                      :style="{backgroundImage: `url(${item.imageUrl})`}">
                    </div>
                    <div class="card-body">
                      <span class="badge badge-info">
                        {{ item.category }}
                      </span>
                      <!-- <h3 class="card-title text-dark">{{item.title}}</h3> -->
                      <h3 class="card-title">
                        <router-link :to="`/product/${item.id}`" class="text-dark">
                          {{ item.title }}
                        </router-link>
                      </h3>
                      <p class="card-text-truncate">{{item.content}}</p>
                      <del class="p-origin-price">{{ item.origin_price | currency }}</del>
                      <p class="h4 card-text mr-3 p-price"> {{ item.price | currency }} </p>
                    </div>
                    <div class="card-footer d-flex border-top-0 bg-white mb-2">
                      <button type="button" class="btn btn-outline-secondary"
                        @click="showIntroduction(item.id)">
                        影片介紹
                      </button>
                      <button type="button" class="btn btn-outline-primary ml-auto"
                        @click="addtoCart(item.id)">
                        加到購物車
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <!-- pagination -->
              <Pagination v-show="category == 'all'"
                :pages="pagination" @emitPages="getProducts"></Pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<style scoped>
.card-text-truncate {
  /* position: relative;
  line-height: 1.5em;
  height: 3em;
  overflow: hidden; */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  max-height: 50px;
  overflow: hidden;
}
</style>

<script>
import Jumbotron from '../Jumbotron';
import Sidebar from '../Sidebar';
import Pagination from '../../Pagination';

export default {
  data() {
    return {
      products: [], // 儲放某頁產品
      allProducts: [],
      pagination: {},
      isLoading: false,
      category: 'all',
    };
  },
  components: {
    Jumbotron,
    Sidebar,
    Pagination,
  },
  methods: {
    getProducts(page = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getAllProduct() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      this.$http.get(url).then((response) => {
        vm.allProducts = response.data.products;
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty,
      };
      vm.$http.post(url, { data: cart }).then(() => {
        vm.$swal({
          title: '已加入購物車',
          type: 'success',
          showConfirmButton: false,
          timer: 2000,
        });
        vm.$bus.$emit('emitGetCart');
      });
    },
    showIntroduction(id) {
      // const heightTop = document.documentElement.scrollTop || document.body.scrollTop;
      // console.log(typeof heightTop);
      // localStorage.setItem('heightTop', heightTop);
      this.$router.push(`/product/${id}`);
    },
    switchCategory(category) {
      this.category = category;
    },
  },
  computed: {
    filterData() {
      const vm = this;
      if (vm.category === 'all') {
        return vm.products;
      }
      return vm.allProducts.filter(item => item.category.match(vm.category));
    },
  },
  created() {
    this.getProducts();
    this.getAllProduct();
    localStorage.setItem('step', 1);
  },
};
</script>



